#mobileMenu{
  width: 320px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  background-color: #111;
  padding: 50px;

  h1{
    width: 100%;
    a{
      img{
        width: 100%;
        margin-bottom: 50px;
      }
    }
  }
  #gnb{
    li{
      a{
        display: block;
        height: 50px;
        font: 16px/50px 'arial';
        color: #bbb;
      }
    }
  }
}